:root {
  --rond-min: 30px;
  --rond-max: 100px;
  --first-color: #2a2a72;
  --second-color: #009ffd;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.loader-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, .1);
}
.box-loader {
  height: 120px;
  width: 120px;
  position: relative;
}
.box-loader .rond {
  position: absolute;
  border-radius: 50%;
  box-shadow: 0 0 15px rgba(0, 0, 0, .1);
}
.box-loader .first {
  height: 30px;
  height: var(--rond-min);
  width: 30px;
  width: var(--rond-min);
  -webkit-animation: anim_one 2s linear infinite;
          animation: anim_one 2s linear infinite;
  background: #2a2a72;
  background: var(--first-color);
}
.box-loader .second {
  left: calc(100% - 100px);
  left: calc(100% - var(--rond-max));
  top: calc(100% - 100px);
  top: calc(100% - var(--rond-max));
  height: 100px;
  height: var(--rond-max);
  width: 100px;
  width: var(--rond-max);
  -webkit-animation: anim_two 2s linear infinite;
          animation: anim_two 2s linear infinite;
  background: #009ffd;
  background: var(--second-color);
}

@-webkit-keyframes anim_one {
  0%,100% { top: 0; left: 0; }
  25% { top: 0; left: calc(100% - 30px); left: calc(100% - var(--rond-min)); }
  50% {
    left: calc(100% - 100px);
    left: calc(100% - var(--rond-max));
    top: calc(100% - 100px);
    top: calc(100% - var(--rond-max));
    height: 100px;
    height: var(--rond-max);
    width: 100px;
    width: var(--rond-max);
    background: #009ffd;
    background: var(--second-color);
  }
  75% { left: 0; top: calc(100% - 30px); top: calc(100% - var(--rond-min)); }
}

@keyframes anim_one {
  0%,100% { top: 0; left: 0; }
  25% { top: 0; left: calc(100% - 30px); left: calc(100% - var(--rond-min)); }
  50% {
    left: calc(100% - 100px);
    left: calc(100% - var(--rond-max));
    top: calc(100% - 100px);
    top: calc(100% - var(--rond-max));
    height: 100px;
    height: var(--rond-max);
    width: 100px;
    width: var(--rond-max);
    background: #009ffd;
    background: var(--second-color);
  }
  75% { left: 0; top: calc(100% - 30px); top: calc(100% - var(--rond-min)); }
}

@-webkit-keyframes anim_two {
  0%,100% {
    left: calc(100% - 100px);
    left: calc(100% - var(--rond-max));
    top: calc(100% - 100px);
    top: calc(100% - var(--rond-max));
    height: 100px;
    height: var(--rond-max);
    width: 100px;
    width: var(--rond-max);
  }
  25% { top: calc(100% - 30px); top: calc(100% - var(--rond-min)); left: 0; }
  50% {
    left: 0;
    top: 0;
    height: 30px;
    height: var(--rond-min);
    width: 30px;
    width: var(--rond-min);
    background: #2a2a72;
    background: var(--first-color);
  }
  75% { left: calc(100% - 30px); left: calc(100% - var(--rond-min)); top: 0; }
}

@keyframes anim_two {
  0%,100% {
    left: calc(100% - 100px);
    left: calc(100% - var(--rond-max));
    top: calc(100% - 100px);
    top: calc(100% - var(--rond-max));
    height: 100px;
    height: var(--rond-max);
    width: 100px;
    width: var(--rond-max);
  }
  25% { top: calc(100% - 30px); top: calc(100% - var(--rond-min)); left: 0; }
  50% {
    left: 0;
    top: 0;
    height: 30px;
    height: var(--rond-min);
    width: 30px;
    width: var(--rond-min);
    background: #2a2a72;
    background: var(--first-color);
  }
  75% { left: calc(100% - 30px); left: calc(100% - var(--rond-min)); top: 0; }
}
